<template>
  <AppBorder :app-info=Video :size=size herder-css="header2">
    <template v-slot:header>
      <div class="fx-ac fx-pj" style="width: 100%">
        <span style="color: white">FLV播放器</span>
        <span class="fx-ac" style="color: white">URL:<input type="text" v-model="url" style="width: 400px"/></span>
        <span>
          <span class="fz20" @click="play">{{ isPlaying ? '⏸️' : '▶️' }}</span>
          <span class="fz20" @click="flv_destroy">⏹</span>
        </span>
        <span></span>
      </div>
    </template>
    <template v-slot:main>
      <div class="fx-ver videoBox">
        <video id="videoElement"></video>
      </div>
    </template>
  </AppBorder>
</template>

<script>
import AppBorder from './AppBorder'
import flvJs from 'flv.js'
import http from '@/axios/index'
import PageManager from '@/utils/PageManager'

export default {
  name: "Video",
  components: {AppBorder},
  mounted() {
  },
  data() {
    return {
      name: "Video",
      Video: PageManager.getAppInfo("Video"),
      size: {
        width: '800px',
        height: '600px'
      },
      flvPlayer: null,
      isPlaying: false,
      url: http.LiveBaseUrl + '/live?port=1935&app=myapp&stream=mystream',
      type: null,
    }
  },
  methods: {
    parseUrl(url) {
      let source = {type: this.type, isLive: false, url};
      if (url.startsWith('blob')) return source;
      if (url.endsWith("mp4")) {
        source.type = "mp4";
      } else if (url.endsWith("flv")) {
        source.type = "flv";
      } else {
        source.isLive = true;
      }
      if (!url.startsWith("http")) source.url = "http://" + url;
      return source;
    },
    createFlvPlayer(url) {
      if (flvJs.isSupported()) {
        const videoElement = document.getElementById('videoElement');
        let source = this.parseUrl(url);
        this.flvPlayer = flvJs.createPlayer(source);
        this.flvPlayer.attachMediaElement(videoElement)
        this.flvPlayer.load();
      } else {
        alert("???!!!");
      }
    },
    play() {
      if (this.isPlaying) {
        this.flv_pause();
      } else this.flv_start();
      this.isPlaying = !this.isPlaying;
    },
    flv_start() {
      if (this.flvPlayer == null) this.createFlvPlayer(this.url);
      this.flvPlayer.play();
    },
    flv_pause() {
      this.flvPlayer.pause();
    },
    flv_destroy() {
      this.flvPlayer.pause();
      this.flvPlayer.unload();
      this.flvPlayer.detachMediaElement();
      this.flvPlayer.destroy();
      this.flvPlayer = null;
    },
    setUrl(url, type) {
      this.url = url;
      this.type = type;
      this.play();
    }
  }
}
</script>

<style scoped>
.videoBox {
  border: 1px saddlebrown solid;
}
</style>