<template>
  <AppBorder :app-info="Clipboard" :herder-css="'header1'" :size=size>
    <template v-slot:header>
      <div class="fx-pj" style="width: 100%">
        <div>
          <span :class="`fa ${Clipboard.shortcut} fz22`"></span>
        </div>
        <div>
          <span>一键复制到剪贴板</span>
        </div>
        <div>
          <button @click="add">添加一行</button>
        </div>
      </div>
    </template>
    <template v-slot:main>
      <div class="tableBox">
        <el-table
            :data="tableData"
            style="width: 100%">
          <el-table-column
              prop="content"
              label="文本"
              width="500">
            <template slot-scope="scope">
              <!--              <el-input v-model="scope.row['content']" :type="'textarea'" :autosize="true"></el-input>-->
              <textarea name="text" id="text" cols="50" rows="2" v-model="scope.row['content']"
                        @blur="autoAdd"></textarea>
            </template>
          </el-table-column>
          <el-table-column
              label="操作"
              width="100">
            <template slot-scope="scope">
              <el-button @click="copy(scope.row)" type="text" size="small">复制</el-button>
              <el-button @click="del(scope.$index)" type="text" size="small">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </template>
  </AppBorder>
</template>

<script>
import PageManager from '@/utils/PageManager'
import AppBorder from "@/components/AppBorder";

export default {
  name: "Clipboard",
  components: {AppBorder},
  data() {
    return {
      Clipboard: PageManager.getAppInfo('Clipboard'),
      tableData: [{content: "[UNK]"}, {content: "[DIA]"}, {content: ""}],
      size: {width: '620px', height: '500px'}
    }
  },
  created() {
    let msgList = localStorage.getItem("msgList");
    let parse = JSON.parse(msgList);
    if (Array.isArray(parse)) {
      this.tableData = parse;
    }
  },
  methods: {
    copy(item) {
      this.$copyText(item.content).then((e) => {
        this.$message.success("复制成功！");
      }, (e) => {
        this.$message.error("复制失败！");
      });
    },
    add() {
      this.tableData.push({content: ""});
      this.sycn2Storage();
    },
    del(idx) {
      this.tableData.splice(idx, 1);
      this.sycn2Storage();
    },
    autoAdd() {
      if (this.tableData[this.tableData.length - 1].content !== "") {
        this.add();
      }
    },
    sycn2Storage() {
      localStorage.setItem('msgList', JSON.stringify(this.tableData));
    }
  }
}
</script>

<style scoped>
.inputContent {
  width: 500px;
}

.tableBox {
  height: 800px;
  overflow-y: scroll;
}

/deep/ .el-table .el-table__cell {
  padding: 1px 0;
  min-width: 0;
  box-sizing: border-box;
  text-overflow: ellipsis;
  vertical-align: middle;
  position: relative;
  text-align: left;
}
</style>