<template>
  <AppBorder :app-info="ChatRoom" :size="size">
    <template v-slot:header>
      <div class="fx-ac">
        <button @click="connect">连接</button>
        <button @click="exit">断开</button>
        <!--        <button @click="dl">dl</button>-->
        <div :class="self.status === 'onLine'?'onLine':'offline'"></div>
        <span class="name"
              @click="openDialog(MsgType.NICK_NAME)">{{ nickNameMap[self.clientName] || self.clientName }}</span>
      </div>
    </template>
    <template v-slot:main>
      <div class="flex main">
        <div class="left">
          <div class="fx-ver memberList">
            <div :class="`member fx-ac fx-pj ${self.curTalk === 'all' ? 'check':''}`" @click="groupChat">
          <span class="fx-ac">
          <HeadImg :item="groupImg"></HeadImg>
          <span class=" fz18">大型多人激情聊天现场</span>
          </span>
              <span class="unread"
                    v-if="unreadMsgCountMap['all']">{{ unreadMsgCountMap['all'] > 0 ? unreadMsgCountMap['all'] : '' }}
              </span>
            </div>
            <div :class="`member fx-ac fx-pj ${self.curTalk === item ? 'check':''}`"
                 v-for="(item,idx) in memberList.list"
                 :key="idx" @click="privateChat(item)">
              <span class="fx-ac">
               <HeadImg :item="headImgMap[item]"></HeadImg>
               <span>{{ nickNameMap[item] || item }}</span>
              </span>
              <span class="unread"
                    v-if="unreadMsgCountMap[item]">{{ unreadMsgCountMap[item] > 0 ? unreadMsgCountMap[item] : '' }}
              </span>
            </div>
          </div>
        </div>
        <div class="right fx-ver">
          <div class="chatName">
            <div class="fx-ac" v-if="self.curTalk === 'all'">
              <HeadImg :item="groupImg" @click.native="openDialog(MsgType.GROUP_MSG)"></HeadImg>
              <span class=" fz22">大型多人激情聊天现场</span>
            </div>
            <div class="fx-ac" v-if="self.curTalk !== 'all'">
              <HeadImg :item="headImgMap[self.curTalk]"></HeadImg>
              <span class="fz22">{{ nickNameMap[self.curTalk] || self.curTalk }}</span>
            </div>
          </div>
          <div class="top" id="talkBox">
            <div class="oneLine" v-for="(item,idx) in groupMsgList" :key="idx">
              <Message :item=item></Message>
            </div>
          </div>
          <div class="middle fx-ac ">
            <el-popover
                placement="top-start"
                width="300"
                trigger="hover">
              <div class="fx-bm emojiBox">
                  <span class="emoji fz18" v-for="(item,idx) in emojiList" :key="idx" @click="text+=item">{{
                      item
                    }}</span>
              </div>
              <span slot="reference" class="middleButton">😃</span>
            </el-popover>
            <span v-if="self.curTalk !== 'all'" class="fa fa-file-image-o fz22 funcButton"
                  @click="openDialog(MsgType.BINARY_IMG)" title="图片"></span>
            <span v-if="self.curTalk !== 'all'" class="fa fa-file-text fz22 funcButton"
                  @click="openDialog(MsgType.BINARY_FILE)" title="文件"></span>
          </div>
          <div class="down">
        <textarea v-model="text" class="textarea fz18" @keyup.enter="sendMsg">
        </textarea>
          </div>
        </div>
        <el-dialog
            title="提示"
            :visible.sync=showBox
            width="30%"
            :modal=false
            :before-close=handleClose>
          <label v-if="type === MsgType.NICK_NAME">
            <span>输入昵称：</span>
            <input type="text" v-model="msg" maxlength="20" @keyup.enter="setNickname" class="textInput"/>
          </label>
          <label v-if="type === MsgType.HEAD_IMG || type === MsgType.GROUP_MSG">
            <div>输入在线图片地址：</div>
            <input type="text" v-model="msg" @keyup.enter="setHeadImg" class="textInput"/>
            <span class="tip" @click="search">懒得找？来这看看</span>
          </label>
          <label v-if="type === MsgType.BINARY_IMG">
            <div>选择发送图片：{{ fileName }}</div>
            <div class="drag-area fx-pac" @dragover="fileDragover" @drop="fileDrop">
              <img :src="previewUrl" alt="预览" width="100%" v-if="fileName">
              <div v-else class="uploader-tips">
                <span>将文件拖拽至此，或</span>
                <label :for="'fileInput'" style="color: #11A8FF; cursor: pointer">点此上传</label>
              </div>
            </div>
          </label>
          <label v-if="type === MsgType.BINARY_FILE">
            <div>选择发送文件：</div>
            <div class="drag-area fx-pac" @dragover="fileDragover" @drop="fileDrop">
              <div v-if="fileName" class="file-name">{{ fileName }}</div>
              <div v-else class="uploader-tips">
                <span>将文件拖拽至此，或</span>
                <label :for="'fileInput'" style="color: #11A8FF; cursor: pointer">点此上传</label>
              </div>
            </div>
          </label>
          <input id="fileInput" type="file" :accept="type === MsgType.BINARY_IMG?'image/*':'*'" style="display: none"
                 @change="()=>{getFile()}">
          <span slot="footer" class="dialog-footer">
        <el-button @click="handleClose">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </span>
        </el-dialog>
        <el-card class="box-card" v-show="percent > 0 && percent < 100">
          <div slot="header" class="clearfix">
            <span>文件上传进度条</span>
          </div>
          <el-progress :text-inside="true" :stroke-width="20" :percentage=percent></el-progress>
        </el-card>
      </div>
    </template>
  </AppBorder>
</template>

<script>
import AppBorder from '../AppBorder'
import client from '../../utils/chatRoom/ConnectWebSocket'
import Message from './Message'
import HeadImg from "./HeadImg";
import pageManager from "@/utils/PageManager";
import MsgType from "@/utils/chatRoom/MsgType";
import Constants from "@/utils/chatRoom/Constants";

export default {
  name: "ChatRoom",
  components: {AppBorder, Message, HeadImg},
  computed: {
    tf() {
      return "matrix(" + this.transform.toString() + ")";
    }
  },
  data() {
    return {
      groupMsgList: client.getMessageList(),
      self: client.getSelf(),
      memberList: client.getMemberList(),
      nickNameMap: client.getNickNameMap(),
      headImgMap: client.getHeadImgMap(),
      privateMsgMap: client.getPrivateMsgMap(),
      unreadMsgCountMap: client.getUnreadMsgCountMap(),
      ChatRoom: pageManager.getAppInfo('ChatRoom'),
      MsgType: MsgType,
      text: "",
      talkBox: null,
      showBox: false,
      msg: '',
      type: '',
      groupImg: null,
      previewUrl: '',
      fileName: '',
      batchFile: null,
      emojiList: ['😀'],
      size: {width: '975px', height: '705px'},
      percent: 0,
      uploadingFile: new Map(),
    }
  },
  created() {
    client.setChatRoom(this);
    this.emojiList = require('emojis-list');
  },
  mounted() {
    this.talkBox = document.getElementById('talkBox');
  },
  methods: {
    upload() {
      const file = document.getElementById("fileUP").files[0];
      if (!file) return;
      const formData = new FormData();
      formData.append("file", file);
      this.$axios({
        url: "http://localhost:8080/download",
        method: "post",
        data: formData
      }).then(response => {
        console.log(response.data);
      })
    },
    dl() {
      this.$axios({
        url: "http://localhost:9443/api/download",
        method: "get",
        responseType: "blob"
      }).then(response => {
            let blob = new Blob([response.data], {
              type: 'application/xml'
            });
            const link = document.createElement('a');
            link.href = window.URL.createObjectURL(blob);
            link.target = "_blank";
            // let filename = response.headers["content-disposition"].split(";")[1].split("filename=")[1];
            link.download = "pom.xml";//decodeURIComponent(filename);
            link.click();
            //释放内存
            window.URL.revokeObjectURL(link.href);
          }
      )
    },
    minimize() {
      pageManager.minimize("ChatRoom");
    },
    exit() {
      if (this.self.status === 'offline') {
        this.$message.error("请先连接！");
        return;
      }
      client.close();
    },
    connect() {
      if (this.self.status === 'onLine') {
        this.$message.error("已登录！");
        return;
      }
      this.groupMsgList.length = 0;
      client.getConnection("chatroom");
      client.onmessage();
    },
    sendMsg() {
      if (client.client === null) {
        this.$message.error("请先连接！");
        return;
      }
      let Msg = {
        msg: this.text,
        destination: "all",
        type: MsgType.TALK,
        sendTime: Date.now()
      }
      if (this.self.curTalk !== 'all') {
        Msg.type = MsgType.PRIVATE_CHAT;
        Msg.destination = this.self.curTalk;
        Msg.self = true;
        Msg.clientName = this.self.clientName;
        this.groupMsgList.push(Msg);
      }
      this.text = '';
      client.checkUnread(this, this.self.curTalk);
      if (this.self.clientName === Msg.destination) return;
      client.writeData(JSON.stringify(Msg));
    },
    privateChat(name) {
      this.self.curTalk = name;
      let msgList = this.privateMsgMap[name];
      if (!msgList) {
        this.privateMsgMap[name] = [];
        this.$set(this.unreadMsgCountMap, name, 0);
      }
      this.unreadMsgCountMap[name] = 0;
      this.groupMsgList = this.privateMsgMap[name];
      client.checkUnread(this, name);
    },
    groupChat() {
      this.self.curTalk = 'all';
      this.unreadMsgCountMap['all'] = 0;
      this.groupMsgList = client.getMessageList();
      client.checkUnread(this, name);
    },
    setNickname() {
      let Msg = {
        msg: this.msg,
        sendTime: Date.now(),
        type: MsgType.NICK_NAME
      }
      this.msg = '';
      client.writeData(JSON.stringify(Msg));
      this.handleClose();
    },
    openDialog(type) {
      if (this.self.status === 'offline') {
        this.$message.error("请先连接！");
        return;
      }
      this.type = type;
      this.showBox = true;
    },
    setHeadImg() {
      let Msg = {
        msg: this.msg,
        sendTime: Date.now(),
        type: this.type
      }
      this.msg = '';
      client.writeData(JSON.stringify(Msg));
      this.handleClose();
    },
    sendImg() {
      let file = this.batchFile
      let Msg = {
        clientName: this.self.clientName,
        sendTime: Date.now(),
        type: MsgType.BINARY_IMG,
      }
      Msg.destination = this.self.curTalk;
      if (this.self.clientName !== Msg.destination)
        client.writeData(JSON.stringify(Msg), file);
      Msg.self = true;
      Msg.img = URL.createObjectURL(file);
      this.groupMsgList.push(Msg);
      this.handleClose();
    },
    async sendFile() {
      let file = this.batchFile
      let Msg = {
        msg: {
          fileName: this.fileName,
          totalSize: file.size,
          chunkSize: file.size,
          chunkTotal: 1,
          chunkCur: 1,
          md5Str: '',
          isAll: true
        },
        clientName: this.self.clientName,
        sendTime: Date.now(),
        type: MsgType.BINARY_FILE,
      }
      Msg.destination = this.self.curTalk;
      if (file.size > Constants.SplitLowLimit) {
        let loading = this.$loading({
          lock: true,
          text: '计算文件MD5中...',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        let chunkNum = Math.ceil(file.size / Constants.ChunkSize);
        Msg.msg.chunkTotal = chunkNum;
        Msg.msg.chunkSize = Constants.ChunkSize;
        let end = await this.computeMD5(file, Constants.ChunkSize);
        Msg.msg.md5Str = end;
        console.log("chunkNum:", chunkNum, "md5Str:", end);
        this.$message.success("文件MD5值为：", end);
        loading.close();
        loading = this.$loading({
          lock: true,
          text: '询问服务器是否存在此文件...',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        let ret;
        try {
          ret = await this.$http.get("/api/checkFile", {
            md5: end
          });
        } catch (e) {
          this.$message.error("请求失败！");
          return;
        } finally {
          loading.close();
        }
        console.log(ret);
        if (ret.code === 200) {
          this.$message.info("服务器已存在此文件。")
          Msg.file = this.$http.HttpBaseUrl + ret.data;
          Msg.type = MsgType.BIG_FILE
          Msg.msg.url = ret.data;
          Msg.msg = {
            url: ret.data,
            fileName: this.fileName,
            sourceName: this.fileName
          }
          client.writeData(JSON.stringify(Msg))
          Msg.self = true;
          this.groupMsgList.push(Msg);
          this.handleClose();
          return
        }
        // if (end === '6') return;
        this.$message.info("开始上传！")
        this.uploadingFile.set(end, {
          chunkNum: chunkNum,
          acKNum: 0
        });
        Msg.msg.isAll = false;
        for (let i = 0; i < chunkNum; i++) {
          let slice = file.slice(i * Constants.ChunkSize, (i + 1) * Constants.ChunkSize);
          Msg.msg.chunkCur = i + 1;
          client.writeData(JSON.stringify(Msg), slice);
          console.log("sendBigFile ", i + 1, 'of', chunkNum);
        }
      }
      if (this.self.clientName !== Msg.destination && Msg.msg.chunkTotal === 1)
        client.writeData(JSON.stringify(Msg), file);
      Msg.self = true;
      Msg.file = true;
      this.groupMsgList.push(Msg);
      this.handleClose();
    },
    //计算文件MD5
    async computeMD5(file, chunkSize) {
      let blobSlice = File.prototype.slice || File.prototype.mozSlice || File.prototype.webkitSlice;
      const SparkMD5 = require('spark-md5');
      let chunks = Math.ceil(file.size / chunkSize),
          spark = new SparkMD5.ArrayBuffer();
      for (let i = 0; i < chunks; i++) {
        let start = i * chunkSize;
        let end = ((start + chunkSize) >= file.size) ? file.size : start + chunkSize;
        let blob = blobSlice.call(file, start, end);
        let buffer = await blob.arrayBuffer();
        spark.append(buffer);
      }
      return spark.end();
    },
    submit() {
      switch (this.type) {
        case MsgType.NICK_NAME:
          this.setNickname();
          break;
        case MsgType.HEAD_IMG:
        case MsgType.GROUP_MSG:
          this.setHeadImg();
          break;
        case MsgType.BINARY_IMG:
          this.sendImg();
          break;
        case MsgType.BINARY_FILE:
          this.sendFile();
          break;
      }
    },
    preview(file) {
      let suffix = file.name.split('.').pop();
      if (!['jpg', 'png', 'jpeg', 'gif'].includes(suffix)) {
        this.$message.error("文件类型有误！");
        this.fileName = null;
        return;
      }
      if (file) this.previewUrl = URL.createObjectURL(file);
    },
    fileDragover(e) {
      e.preventDefault()
    },
    getFile(file) {
      file = file || document.getElementById('fileInput').files[0];
      if (!file) return;
      this.batchFile = file;
      this.fileName = file.name;
      if (this.type === MsgType.BINARY_IMG) this.preview(file);
    },
    fileDrop(e) {
      e.preventDefault()
      const file = e.dataTransfer.files[0] // 获取到第一个上传的文件对象
      this.getFile(file);
    },
    closeApp() {
      pageManager.closeApp("ChatRoom")
    },
    search() {
      this.$emit('search', 'https://image.baidu.com/search/index?ct=201326592&z=0&s=0&tn=baiduimage&ipn=r&word=%E5%A4%B4%E5%83%8F%E4%BA%8C%E6%AC%A1%E5%85%83&pn=&spn=&istype=2&ie=utf-8&oe=utf-8&cl=2&lm=-1&st=-1&fr=&fmq=1461834053046_R&ic=0&se=&sme=&width=&height=&face=0&cs=&os=&objurl=&di=&cg=head&gsm=5a');
    },
    playVideo(url, type) {
      this.$emit('playVideo', url, type);
    },
    handleClose(done) {
      if (this.previewUrl) {
        URL.revokeObjectURL(this.previewUrl);
      }
      this.fileName = null;
      this.showBox = false;
      if (typeof done === 'function') {
        done();
      }
    }
  }
}
</script>

<style scoped>
@import "../../assets/chatroom.css";

.box-card {
  width: 200px;
  position: fixed;
  right: -202px;
  top: 0;
}
</style>